import React, { lazy, Suspense, useEffect } from 'react';
import './App.css';
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import * as userActions from './redux/actions/user'
import { Switch, withRouter, Route } from 'react-router-dom'
import CircularProgress from '@material-ui/core/CircularProgress';

const CashboxCinema = lazy(() => import('./pages/CashboxCinema'));
const Seance = lazy(() => import('./pages/Seance'));
const Main = lazy(() => import('./pages/Main'));
const Scanner = lazy(() => import('./pages/Scanner'));
const Cashbox = lazy(() => import('./pages/Cashbox'));
const CheckElsom = lazy(() => import('./pages/CheckElsom'));
const Event = lazy(() => import('./pages/Event'));
const AppBar = lazy(() => import('./component/AppBar'));
const Drawer = lazy(() => import('./component/Drawer'));
const Dialog = lazy(() => import('./component/Dialog'));
const ScannerCinema = lazy(() => import('./pages/ScannerCinema'));
const Turnstile = lazy(() => import('./pages/Turnstile'));
const TurnstileCinema = lazy(() => import('./pages/TurnstileCinema'));
const Unloading = lazy(() => import('./pages/Unloading'));
const Detection = lazy(() => import('./pages/Detection'));
export const mainWindow = React.createRef();

const App = (
    (props) =>{
        const { checkAuthenticated, setStatus } = props.userActions;
        useEffect(()=>{(async ()=>{
            await checkAuthenticated();
            await setStatus()
        })()},[])
        return (
              <div ref={mainWindow} className='App'>
                  <Suspense fallback={null}>
                      <AppBar history={props.history} location={props.location}/>
                  </Suspense>
                  <Suspense fallback={null}>
                      <Drawer history={props.history} location={props.location}/>
                  </Suspense>
                  <div className='App-body'>
                      <Switch>
                          <Route  path='/' exact component={WaitingComponent(Main, props.history, props.location)}/>
                          <Route  path='/unloading' component={WaitingComponent(Unloading, props.history, props.location)}/>
                          <Route  path='/elsom' component={WaitingComponent(CheckElsom, props.history, props.location)}/>
                          <Route  path='/scanner' component={WaitingComponent(Scanner, props.history, props.location)}/>
                          <Route  path='/event' component={WaitingComponent(Event, props.history, props.location)}/>
                          <Route  path='/cashbox' component={WaitingComponent(Cashbox, props.history, props.location)}/>
                          <Route  path='/seance' component={WaitingComponent(Seance, props.history, props.location)}/>
                          <Route  path='/cashboxcinema' component={WaitingComponent(CashboxCinema, props.history, props.location)}/>
                          <Route  path='/scannercinema' component={WaitingComponent(ScannerCinema, props.history, props.location)}/>
                          <Route  path='/turnstile' component={WaitingComponent(Turnstile, props.history, props.location)}/>
                          <Route  path='/turnstilecinema' component={WaitingComponent(TurnstileCinema, props.history, props.location)}/>
                          <Route  path='/detection' component={WaitingComponent(Detection, props.history, props.location)}/>
                      </Switch>
                  </div>
                  <Suspense fallback={null}>
                      <Dialog/>
                  </Suspense>
                  {/*load?
                      <div style={{width: '100%', height: '100vh', display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
                          <CircularProgress/>
                      </div>
                      :
                      null*/
                  }
              </div>
        );
  })

function WaitingComponent(Component, history, location) {
    return props => (
        <Suspense fallback={<div>Loading...</div>}>
            <Component {...props}  history={history} location={location}/>
        </Suspense>
    );
}

function mapStateToProps (state) {
    return {
        user: state.user,
        app: state.app,
    }
}

function mapDispatchToProps(dispatch) {
    return {
        userActions: bindActionCreators(userActions, dispatch),
    }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(App));
