import { SHOW_DRAWER, SHOW_LOAD } from '../constants/app'

const initialState = {
    drawer: false,
    load: false
}

export default function mini_dialog(state = initialState, action) {
    switch (action.type) {
        case SHOW_DRAWER:
            return {...state, drawer: action.payload}
        case SHOW_LOAD:
            return {...state, load: action.payload}
        default:
            return state
    }
}